#ride-history-modal {
  background: #fff;
  padding: 35px;
  border-radius: 2px;
  top: 30%;
  li {
    color: #333;
    font-size: 18px;
  }
}
.ride-history {
  a {
    text-decoration: underline;
    color: #333;
    font-weight: normal;
    font-size: 16px;
    &:hover {
      text-decoration: none;
    }
  }
}
.modal {
  max-width: 620px;
  position: absolute;
  float: left;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
