/*  FOOTER  */

footer {
  margin-top: 20px;
  width: 100%;
}

.footer a {
  font-weight: normal;
  color: black;
}

.footer a:hover {
  text-decoration: underline;
}

footer ul li{
  color: black;
  display: inline-block;
  margin-right: 20px;
  margin-top: 20px;
  font-size: 20px;
}

.footer-callout {
  font-size: 14px;
  margin-bottom: 20px;
  color: #888;
}

.footer-callout a {
  text-decoration: none;
  color: #888;

}
.footer-callout a:hover {
  text-decoration: underline;
}

span.brand-callout  {
  letter-spacing: 2px;
  text-transform: capitalize;
  font-size: 22px;
}
