
input {
  background: none;
  box-shadow: none;
  border-radius: 0px;
}

input:focus {
  // background-color: yellow;
}

/* MESSAGES */

#offline-warning {
  margin-bottom: 250px;
}

#configure-commute {
  h2 {
    position: relative;
    right: 10px;
    text-transform: uppercase;
    font-size: 40px;
  }
}

.configure-commute-explainer {
  width: 420px;
  position: relative;
  float: right;
  top: -200px;
  right: 245px;
  margin-top: 23px;

  p {
    font-size: 20px;
    line-height: 32px;
    margin-bottom: 38px;
  }
}

a:link {
  text-decoration: none;
  font-weight: bold;
}

li {
  font-size: 40px;
  line-height: 40px;
  color: #eee;
  list-style: none;
}

li .countdown {
  font-size: 50px;
  float: right;
  font-family: 'IBM Plex Sans', 'Lucida Console', 'Monaco', monospace;
  letter-spacing: 0.1em;
}

li.arrival-time {
  padding: 20px;
  margin: 5px 0;
  box-shadow: 1px 1px 2px 0 #666;
}


li.arrival-time .line-detail {
  font-size: 20px;
  margin: 0 0 -15px 0;
  height: 50px;
  .station-arrival {
    position: relative;
    top: -10px;
  }
}

// NOTE: the no route data is always there, just positioned behind the others
// Great victory!
li.blank-arrival-am, li.blank-arrival-pm {
  background-color: #999;
  position: relative;
  z-index: -10;
  top: 0px;
  display: block;
}

li.arrival-time:not(.blank-arrival) {
  position: relative;
  top: -85px;
}

.blank-arrival {
  width: 956px;
}

li.arrival-time .line-detail:hover {
  text-decoration: underline;
}

.trainData-am, .trainData-pm {
  width: 1000px;
  clear:both;
}

div.trainData-am, div.trainData-pm {
  margin-top: 20px;
}

ul.trainData-am, ul.trainData-pm {
  margin-top: 15px;
}

.station-setting {
  line-height: 36px;
  font-size: 25px;
  position: relative;
}

img.airport-icon {
  position: relative;
  top: 16px;
  padding: 10px 0 10px 10px;
}

.station-setting.departure {
  margin-top: 0;
  margin-bottom: 10px;
  margin-left: 10px;
  width: 250px;
  right: 0;
}

// #am-station-search, #pm-station-search, #am-destination-station, #pm-destination-station {}
.station-setting.destination {
  float: right;
  width: 300px;
  left: 18px;
}

.select-filter {
  font-size: 25px;
  min-width: 260px;
}

.am-train-filter, .pm-train-filter {
  float: right;
}

.am-commute-destination, .pm-commute-destination {
  margin-left: 10px;
}

.compass {
  /* background: url(http://i.imgur.com/44nyA.jpg); */
  background-size: cover;
  padding-left: 10px;
  top: 6px;
  position: relative;
}
