/* BOARDED STATUS */

.boarded-wrapper {
  width: 100%;
  line-height: 40px;
  font-size: 20px;
  font-weight: bold;
  z-index: 10;
  position: relative;
}

.animate-boarded {
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
  animation-name: colorPulse;
}

@keyframes colorPulse {
  0% {
    background-color: #4b0303;
  }
  50% {
    background-color: #e40101;
  }
  100% {
    background-color: #4b0303;
  }
}

.boarded-arrived {
  -webkit-animation-play-state: paused !important;
  -moz-animation-play-state: paused !important;
  -o-animation-play-state: paused !important;
  animation-play-state: paused !important;
  background-color: #12d463;
}

.boarded-status {
  width: 1000px;
  margin: 0 auto;
  height: 50px;
  line-height: 50px;
  font-size: 20px;
  font-weight: bold;
  color: #eee;
}

.boarding-close-icon {
  float: right;
  margin-top: 7px;
  cursor: pointer;
}

.fixedElement {
  background-color: #c0c0c0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 100;
}
