.weather-data-all {
  width: 100%;
  padding-bottom: 20px;
  height: 90px;
}

.weather-data-container.current {
  display: inline-block;
  float: right;

  .weather-data {
    left: 510px;
    position: relative;

    .weather-sparkline-box {
      float: left;
      height: 95px;
      width: 110px;
      font-size: 20px;
      text-align: center;
      border-left: 1px solid #999;
      border-top: 1px solid #999;
      border-bottom: 1px solid #999;
      box-sizing: border-box;
      -moz-box-sizing: border-box;
      -webkit-box-sizing: border-box;

      #temp-trends {
        width: 110px;
        border-bottom: 1px solid #999;
      }
      .min-max-temp-detail {
        font-weight: bold;
      }
      span {
        font-size: 14px;
        position: relative;
      }
      .forecast-timeframe {
        float: left;
        left: 5px;
      }
      .forecast-max {
        float: right;
        margin-right: 6px;
      }
      .forecast-min {
        float: right;
        margin-right: 6px;
      }
    }
    .weather-details-box {
      border-top: 1px solid #999;
      border-left: 1px solid #999;
      box-sizing: border-box;
      -moz-box-sizing: border-box;
      -webkit-box-sizing: border-box;
      float: left;
      height: 95px;
      width: 170px;
      font-size: 20px;
      text-align: center;

      .weather-wind-box {
        display: inline-block;
        position: relative;
        top: 5px;

        img.wind-icon {
          height: 30px;
          width: 30px;
          margin-right: 6px;
          top: 7px;
          position: relative
        }
      }
      .weather-humidity-box {
        display: inline-block;
        position: relative;
        height: 33px;
        top: 15px;
        background-color: #555;
        width: 170px;
        color: white;
        padding-top: 11px;
        font-weight: 100;
        .humidity {
          font-size: 16px;
        }
      }
    }

    .weather-icon-box {
      border-top: 1px solid #999;
      height: 80px;
      width: 110px;
      box-sizing: border-box;
      -moz-box-sizing: border-box;
      -webkit-box-sizing: border-box;
      float: left;
      font-size: 20px;
      text-align: center;

      .weather-icon {
        float: left;
        position: relative;
        left: 32px;
      }
      .description-container {
        display: inline-block;
        width: 110px;
        height: 44px;
        text-align: center;
        font-size: 22px;
        background-color: rgb(0, 195, 255);
        border-bottom: 1px solid #999;
        box-sizing: border-box;
        -moz-box-sizing: border-box;
        -webkit-box-sizing: border-box;

        .icon-description {
          position: relative;
          top: 10px;
          color: white;
        }
      }
    }

    .weather-temp-box {
      height: 65px;
      width: 80px;
      float: right;
      background-color: #555;
      color: white;
      padding: 15px;

      .weather-temp {
        font-size: 40px;
        font-weight: bold;
        display: inline;
        position: relative;
        left: 0;
        top: 0;
        .unit-indicator {
          font-weight: 100;
        }
      }

      .weather-city {
        text-align: center;
        position: relative;
        font-size: 16px;
        font-weight: lighter;
        left: 10px;
      }
    }

  }
}
