/*  ALERTS  */

h2.system-alerts {
  margin: 20px 0;
  font-size: 35px;
}

.all-system-alerst {
  margin-top: 60px;
}

ul.system-alerts, ul.system-overall, ul.station-alerts-am, ul.station-alerts-pm {
  clear: both;
}

.event-details {
  span.alert-timeframe {
    font-weight: medium;
    font-size: 20px;
  }
}
.station-alerts-am, .station-alerts-pm, .system-alerts {
  clear: both;
  margin-bottom: 20px;
  // border: 1px solid #ccc;
  // padding: 20px;
  // border-radius: 2px;
}
.station-alerts-am h2, .station-alerts-pm h2, .system-alerts h2 {
  margin-bottom: 20px;
  margin-top: 60px;
}

li.alertbox {
  padding: 0 20px 20px 20px;
  margin-top: 5px;
  position: relative;
  box-shadow: 1px 1px 2px 0 #666;
}

.alertbox h2 {
  font-size: 32px;
  max-width: 910px;
}

.alertbox p {
  margin-top: 12px;
  font-size: 18px;
  line-height: 25px;
  color: white;
}

.alertbox .icon.close-icon {
  position: absolute;
  top: 20px;
  right: 20px;
  cursor: pointer;
}

.alertbox .sev-score {
  position: relative;
  top: 5px;
  font-size: 16px;
  left: 3px;
}

.impact-details b {
  font-size: 20px;
}